import React from 'react';
import 'animate.css'
import '../headersty.css'
import Typist from 'react-typist';
import {
    Jumbotron,
    Row,
    Card,
    CardText,
    CardBody,
    CardTitle
} from 'reactstrap';

const Greeting = ({ class_project, SRCs, titulo, content }) =>
    <Card className="animate__animated animate__backInLeft cartao ">
        <img className={class_project} src={SRCs} alt="" />
        <CardBody>
            <CardTitle tag="h5">{titulo}</CardTitle>
            <CardText>{content}</CardText>
        </CardBody>
    </Card>;
function Home() {
    return (
        <div className="destaque">
            <div>
                <Typist className="digitacao">
                    Seja bem vindo ao  meu Portifolio.
                </Typist>
            </div>
            <Jumbotron className="rest-ts">
                
                <Row xs="1" sm="2" md="4">
                    <Greeting class_project="technologies"
                        SRCs="data:image/png;base64,"
                        titulo="NodeJS"
                        content="O NodeJS é um dos softwares open-source mais incriveis, pode ser usado tanto no Backend quanto no Frontend. Esta tecnologia deixar infinitas possibilidades de fazer projetos com javascript. Sou apaixonado por NodeJS, os meus maiores projetos particulares utilizaram-no." />
                    <Greeting class_project="technologies-in"
                        SRCs="data:image/png;base64,"
                        titulo="ReactJS"
                        content="O React é uma biblioteca JavaScript open-source, focada na experiencia do client, ou seja, o Frontend. Uma de suas principais caracteristicas é a possibiblidade de criar seus próprios componentes no HTML com Javascript. Esta pagina web foi criada utilizando o React." />
                    <Greeting class_project="technologies-in-Obr"
                        SRCs="data:image/png;base64,"
                        titulo="OBR"
                        content="A Olimpiadas Brasileiras de Robótica, incetivar os alunos a programarem e criarem robôs que realizam ações de forma autônoma. Na edição de 2019 tive oportunidade de programar no arduino em C++. Já na edição de 2020 a minha equipe ficou em segundo lugar, da estadual do Rio de Janeiro." />
                    <Greeting class_project="technologies-in"
                        SRCs="data:image/jpeg;base64,"
                        titulo="Mini Sumô"
                        content="Mini Sumô é uma categoria de combate entre robôs completamente autônomos, na qual o objetivo se assemelha do objetivo de um sumô entre humanos. Este é o projeto mais recente, Iremos competir em 2021. Estou desenvolvendo este projeto com a mesma equipe da OBR, a SpeedRuneers. " />
                </Row>
            </Jumbotron>
        </div>
    );
}
export default Home;